.footer-div {
    margin: 0 auto;
    width: 75%;
    padding-top: 15px;
}

.comp_footer a {
    text-decoration: none;
    transition: 0.2s;
}

.comp_footer a:hover {
    color: #9D9D9D;
    transition: 0.2s;
}

.comp_footer p {
    font-size: 20px;
}

.comp_footer .column {
    padding: 25px;
}

.developed-by {
    text-align: center;
    margin-top: 30px;
}
/* Overall */
body {
    background-color: #CBCBCB !important;
}

/* Sidebar */
.username-display {
    text-align: center;
}

.stats, .library {
    width: 100%;
    border-radius: 0 !important;
    font-size: 20px;
}

.stats:hover, .library:hover {
    background-color: #27496d;
}

.profile-image img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
    margin-top: 1em;
}

.volforce-display {
    text-align: center;
}

/* Recent Songs */
.comp_recentsongs {
    width: 100%;
}

.comp_recentsongs table tr:hover td {
    background-color: #1C4269;
    transition: 0.1s;
}

.comp_recentsongs table tr {
    transition: 0.1s;
}

.recentscores, .volforce-line, .grade-pie, .topscores, .library-content {
    border-radius: 10px;
    overflow: hidden;
}

/* Top Scores */
.comp_topscores {
    width: 100%;
}

.comp_topscores table .number {
    width: 5%;
}

.tabcontent {
    display: none;
    -webkit-animation: fadeEffect 0.5s;
            animation: fadeEffect 0.5s;
}

@-webkit-keyframes fadeEffect {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes fadeEffect {
    from {opacity: 0;}
    to {opacity: 1;}
}

.active {
    background-color: #27496d;
    outline: none;
}

#tabProfile {
    display: block;
}

/* Library */
.comp_library {
    width: 100%;
    overflow-x: auto;
}

.comp_library table {
    width: 100%;
}

.comp_library table tr td {
    padding: 20px;
}

.comp_library img {
    vertical-align: middle;
    width: 80%;
    border-radius: 7px;
}

.comp_library .jacket {
    width: 8%;
    text-align: middle;
}

.comp_library .song {
    width: 35%;
}

.diffNOV, .diffADV, .diffEXH, .diffMXM {
    width: 10%;
}

.comp_library table {
    border-collapse: collapse;
}

@media only screen and (max-width: 1024px) {
    .profile-image img {
        margin-top: 0px;
    }

    .recentscores, .volforce-line, .grade-pie, .topscores, .library-content {
        margin: 0 !important;
        border-radius: 0;
    }
}
.comp_songtable {
    margin-left: 100px;
    margin-right: 100px;
    margin-top: 20px;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 15px;
}

.songtableclass {
    overflow-x: auto;
}

.comp_songtable img {
    vertical-align: middle;
    width: 80%;
    border-radius: 7px;
}

.comp_songtable .jacket {
    width: 8%;
    text-align: middle;
}

.comp_songtable th {
    font-family: 'Roboto Slab', serif;
    font-weight: bold;
}

.comp_songtable td {
    font-family: 'Roboto', sans-serif;
    padding: 20px;
}

/* Song List Search */
.form-flex-item {
    margin-right: 10px;
    margin-left: 10px;
    margin-bottom: 15px;
}

.comp_songtablesearch button {
    width: 50%;
    font-size: 20px;
    transition: 0.2s;
    overflow: hidden;
}

.button-flex {
    text-align: right;
}

.comp_songtablesearch button:hover {
    background-color: #C8C8C8;
}

@media only screen and (max-width: 1025px) {
    .comp_songtable {
        margin: 0;
        border-radius: 0;
    }
}
.comp_register form {
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    width: 25%;
    padding: 25px;
    border-radius: 10px;
}

.comp_register form input {
    margin-top: 10px;
    width: 100%;
    padding: 5px;
    font-size: 20px;
    border-radius: 5px;
}

.comp_register form .form-item {
    margin-top: 15px;
}

.comp_register form button {
    width: 100%;
    margin-top: 20px;
    transition: 0.2s;
}

.comp_register form button:hover {
    transition: 0.2s;
    background-color: #00747F;
}

@media only screen and (max-width:  1024px) {
    .comp_register form {
        width: 50%;
    }
}

@media only screen and (max-width: 769px) {
    .comp_register form {
        width: 75%;
    }
}

@media only screen and (max-width: 769px) {
    .comp_register form {
        width: 100%;
        margin: 0;
        border-radius: 0;
    }
}
/* Home Top */

.comp_hometop {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 300px;
    width: 100%;
}

.register-btn {
    font-size: 20px;
    transition: 0.2s;
}

.register-btn:hover {
    background-color: #00747F;
    transition: 0.2s;
}

/* Home Description */
.comp_homedescription .column {
    border-radius: 7px;
    padding: 1.5em;
}

.comp_homedescription .column p {
    line-height: 30px;
}

.comp_homedescription .column h3, .icon-image {
    text-align: center;
}

.comp_homedescription .column img {
    border-radius: 50%;
}

.comp_homedescription .column {
    margin-left: 100px;
    margin-right: 100px;
    margin-top: 25px;
    margin-bottom: 25px;
}

@media only screen and (max-width: 769px) {
    .comp_homedescription .column {
        margin-right: 15px;
        margin-left: 15px;
    }
}
.comp_login form {
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    width: 25%;
    padding: 25px;
    border-radius: 10px;
}

.comp_login form input {
    margin-top: 10px;
    width: 100%;
