/* Using Fonts */
.font-roboto-slab {
    font-family: 'Roboto Slab', serif;
    font-weight: bold;
}

.font-roboto {
    font-family: 'Roboto', sans-serif;
}

.font-source {
    font-family: 'Source Sans Pro', sans-serif;
}

.font-oswald {
    font-family: 'Oswald', sans-serif;
}

/* Flexbox Stuff */
.row {
    display: flex;
    flex-direction: row;
}

.row .column {
    flex: 1 1;
}

.row .column.two-times {
    flex: 2 2;
}

.row .column.three-times {
    flex: 3 3;
}

.row .column.four-times {
    flex: 4 4;
}

.row .column.order-1 {
    order: 1
}

.row .column.order-2 {
    order: 2;
}

.row .column.order-3 {
    order: 3;
}

.row .column.order-4 {
    order: 4;
}

/* Headers */

h1 {
    font-size: 35px;
    font-weight: bold;
}

h2 {
    font-size: 30px;
    font-weight: bold;
}

h3 {
    font-size: 25px;
    font-weight: bold;
}

/* Tables */

table {
    width: 100%;
    text-align: left;
    border-collapse: collapse;
}

td, th {
    padding: 10px;
}

td {
    border-top: 1px solid;
}

/* Forms */
.form-input {
    padding: 10px;
    border-radius: 5px;
    width: 100%;
}

.btn {
    padding: 10px;
    border-radius: 5px;
    font-weight: bold;
    border: none;
}

/* Footer */
.comp_footer {
    min-height: 250px;
}

.nav-right {
    float: right;
}

@media only screen and (max-width: 1024px) {
    .row {
        display: block;
    }

    .recentscores, .volforce-line, .grade-pie, .topscores {
        margin: 0;
    }

    .nav-right {
        float: none;
        text-align: center;
        margin-top: 15px;
    }
}

/* CSS file for modifying the specifics of the navigation bar */

nav .link {
    text-decoration: none;
    padding: 0 15px 0 15px;
    transition: 0.2s;
}

nav .link:hover {
    color: #9D9D9D;
    transition: 0.2s;
}

nav .link:focus {
    color: #9D9D9D;
}
